<template>
    <v-container class="pa-0 ma-0" fluid>
        <v-form v-model="validForm">
            <v-text-field
                    v-if="showUsernameField"
                    v-model="username"
                    :rules="[rules.requiredS, rules.min4Char, rules.validUsername, rules.max14Char]"
                    :label="usernameText"
                    counter
                    maxlength="14"
                    required
                    @input="formUpdated"
            ></v-text-field>

            <div v-if="!showUsernameField" style="margin-top: 10px"></div>
            <v-dialog
                    ref="dialog"
                    v-model="modal"
                    :return-value.sync="birthDate"
                    persistent
                    max-width="500px"
            >
                <template v-slot:activator="{ on }">
                    <v-text-field
                            v-model="birthDateInTextMonthFormat"
                            :label="birthDateText"
                            append-icon="mdi-calendar"
                            :rules="[rules.required, rules.minAge]"
                            readonly
                            v-on="on"
                            required
                    ></v-text-field>
                </template>
                <v-date-picker v-model="birthDate" scrollable width="97%" :locale="localeLang">
                    <v-spacer></v-spacer>
                    <v-btn text color="primary" @click="modal = false">{{cancelText}}</v-btn>
                    <v-btn text color="primary" @click="$refs.dialog.save(birthDate)">OK</v-btn>
                </v-date-picker>
            </v-dialog>

            <v-select
                    v-model="gender"
                    :rules="[rules.required]"
                    style="margin-top: 15px"
                    :items="getGenderOptionsAsArray"
                    item-value="id"
                    item-text="option"
                    :label="genderText"
                    dense
                    return-object
                    @input="formUpdated"
            >

                <template v-slot:item="{item}">
                    <v-avatar>
                        <img :src="item.img" style="height: 60%; width: auto"/>
                    </v-avatar>
                    {{ item.text }}
                </template>
                <template v-slot:selection="{item, select, selected }">
                    <v-avatar>
                        <img :src="item.img" style="height: 50%; width: auto"/>
                    </v-avatar>
                    {{ item.text }}
                </template>
            </v-select>

            <v-textarea
                    style="margin-top: 15px"
                    outlined
                    v-model="description"
                    :rules="[rules.requiredS, rules.min20Char]"
                    :label="tellUsALittleAboutYouText"
                    required
                    counter
                    maxlength="140"
                    @input="formUpdated"
            ></v-textarea>
        </v-form>
        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import Constants from "../store/Constants";
    import {mapGetters} from "vuex";

    const minimumAge = Constants.RULE_MIN_VALUE_AGE;

    // Selector de información de perfil
    // @group Auxiliares
    export default {
        name: "ProfileInfoSelector",
        props: {
            // Indicación de si mostrar o no el campo para introducir el nombre de usuario
            showUsernameField: {
                type: [Boolean],
                required: false,
            },
            // Indicación de si mostrar o no el campo para introducir el nombre de usuario
            birthDateInitialValue: {
                type: [Date],
                required: true,
            },
            // Indicación de si mostrar o no el campo para introducir el nombre de usuario
            genderInitialValue: {
                type: [String],
                required: false,
            },
            // Indicación de si mostrar o no el campo para introducir el nombre de usuario
            descriptionInitialValue: {
                type: [String],
                required: false,
            }
        },
        data() {
            return {
                genderText: null,
                toContinueText: null,
                tellUsALittleAboutYouText: null,
                birthDateText: null,
                fillYourProfileText: null,
                usernameText: null,
                cancelText: null,
                snackbarText: null,
                snackbar: false,
                closeText: null,
                username: '',
                birthDate: null,
                birthDateInTextMonthFormat: null,
                firstTimeOpenCalendar: true,
                menu: false,
                modal: false,
                description: '',
                gender: null,
                validForm: false,
                rules: {
                    required: value => !!value || this.$i18n.t('messages.RULE_TEXT_MANDATORY_FIELD'),
                    requiredS: value => (!!value && value != '') || this.$i18n.t('messages.RULE_TEXT_MANDATORY_FIELD'),
                    max14Char: value => (value && value.length <= Constants.RULE_MAX_VALUE_CHARACTERS_USERNAME) || this.$i18n.t('messages.RULE_TEXT_MAX_VALUE_CHARACTERS_USERNAME'),
                    min4Char: value => (value && value.length >= Constants.RULE_MIN_VALUE_CHARACTERS_USERNAME || value.length == 0) || this.$i18n.t('messages.RULE_TEXT_MIN_VALUE_CHARACTERS_USERNAME'),
                    min20Char: value => (value && value.length >= Constants.RULE_MIN_VALUE_CHARACTERS_USER_DESCRIPTION || value.length == 0) || this.$i18n.t('messages.RULE_TEXT_MIN_VALUE_CHARACTERS_USER_DESCRIPTION'),
                    minAge: () => {
                        return this.validBirthDate() || this.$i18n.t('messages.RULE_YOU_HAVE_TO_BE_AT_LEAST_X_YEARS_OLD_TO_REGISTER', {age: Constants.RULE_MIN_VALUE_AGE});
                    },
                    validUsername: value => {
                        const pattern1 = /[a-zA-Z_]+[a-zA-Z0-9_]*/
                        const pattern2 = /^_/
                        const pattern3 = /_[_]+/
                        return (pattern1.test(value) && !pattern2.test(value) && !pattern3.test(value)) || this.$i18n.t('messages.TEXT_USERNAME_NOT_VALID')
                    }
                }
            }
        },
        methods: {
            // @vuese
            // Evalúa la validez de los datos introducidos
            formUpdated: function () {
                let isValid = this.validForm;
                let data;

                if (isValid && this.validBirthDate()) {
                    data = {
                        'isValid': true,
                        'username': this.username,
                        'birthDate': new Date(this.birthDate).setHours(0, 0, 0),
                        'gender': this.gender,
                        'description': this.description
                    };

                } else {
                    data = {
                        'isValid': false
                    };
                }

                // Se genera al actualizar la información
                // @arg El parámetro es un Objeto que representa la información del formulario y la validez de la misma
                this.$emit('dataValidityUpdate', data);
            },
            // @vuese
            // Evalúa si la fecha de nacimiento introducida cumple con las condiciones de usuario de la aplicación
            validBirthDate: function () {
                const bDate = new Date(this.birthDate);
                const currentDate = new Date();
                currentDate.setHours(0, 0, 0);
                let validAge;
                if (currentDate.getFullYear() - bDate.getFullYear() < minimumAge) {
                    validAge = false;
                } else if (currentDate.getFullYear() - bDate.getFullYear() == minimumAge) {
                    if (currentDate.getMonth() < bDate.getMonth()) {
                        validAge = false;
                    } else if (currentDate.getMonth() == bDate.getMonth()) {
                        if (currentDate.getDate() < bDate.getDate()) {
                            validAge = false;
                        } else {
                            validAge = true;
                        }
                    } else {
                        validAge = true;
                    }
                } else {
                    validAge = true;
                }
                return validAge;
            }
        },
        computed: {
            // @vuese
            // Devuelve el idioma local en i18n para aplicarlo al selector de fecha
            localeLang: function () {
                return this.$i18n.locale;
            },
            ...mapGetters('user', ['getGenderOptionsAsArray'])
        },
        watch: {
            // @vuese
            // Observa la fecha de nacimiento y cuando se actualiza, guarda su representación en texto i18n
            birthDate: function () {
                this.birthDateInTextMonthFormat = this.$i18n.d(new Date(this.birthDate), 'short');
                this.formUpdated();
            },
            validForm: function () {
                this.formUpdated();
            }
        },

        created() {
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
            this.genderText = this.$i18n.t('messages.TEXT_GENDER');
            this.toContinueText = this.$i18n.t('messages.TEXT_TO_CONTINUE');
            this.tellUsALittleAboutYouText = this.$i18n.t('messages.TEXT_TELL_US_A_LITTLE_ABOUT_YOU');
            this.birthDateText = this.$i18n.t('messages.TEXT_BIRTHDATE');
            this.fillYourProfileText = this.$i18n.t('messages.TEXT_FILL_YOUR_PROFILE');
            this.usernameText = this.$i18n.t('messages.TEXT_USERNAME');
            this.cancelText = this.$i18n.t('messages.TEXT_CANCEL');
        },
        mounted() {
            if (this.birthDateInitialValue) {
                this.birthDate = new Date(this.birthDateInitialValue).toISOString().substr(0, 10);
            }
            if (this.genderInitialValue) {
                this.gender = this.genderInitialValue;
            }

            if (this.descriptionInitialValue) {
                this.description = this.descriptionInitialValue;
            }

            this.formUpdated();
        }
    }
</script>

<style scoped>

</style>