<template>
    <div class="img-back" style="position: relative; height: 100%!important;">
        <v-container class="login" style="position: relative; height: 100%; max-width: 550px">
            <v-row class="justify-center text-center">
                <v-card-title class="text-center justify-center pa-3 white--text">
                    {{fillYourProfileText}}
                </v-card-title>

                <v-col cols="12" style="padding-bottom: 0px">
                    <v-card style="padding-bottom: 8px" class="text--center justify-center">
                        <div align="center" style="padding-top: 30px">
                            <img style="width: 60px" src="/LogoWeBabble.png"/>
                        </div>

                        <v-container class="pa-3" style="padding-top: 10px!important;">
                            <profile-info-selector
                                    :show-username-field="true"
                                    :birth-date-initial-value="birthDatePreview"
                                    @dataValidityUpdate="isDataValid($event)">
                            </profile-info-selector>

                            <v-btn block color="primary" @click="setProfileInfo" :disabled="buttonIsDisabled">
                                {{toContinueText}}
                            </v-btn>
                        </v-container>
                    </v-card>
                </v-col>
            </v-row>

            <!-- Error snackbar -->
            <v-snackbar
                    v-model="snackbar"
                    :timeout="4000"
            >
                {{ snackbarText }}
                <v-btn
                        color="primary"
                        text
                        @click="snackbar = false"
                >
                    {{closeText}}
                </v-btn>
            </v-snackbar>
            <!--End snackbar-->
        </v-container>
    </div>
</template>

<script>

    import ProfileInfoSelector from "@/components/ProfileInfoSelector";
    import {mapActions} from 'vuex';
    import Constants from "../../store/Constants";

    const minimumAge = Constants.RULE_MIN_VALUE_AGE;

    // Página de registro con formulario de información de perfil
    // @group Cuenta
    export default {
        name: "FillYourProfileInfo",
        components: {ProfileInfoSelector},
        data() {
            return {
                genderText: null,
                toContinueText: null,
                tellUsALittleAboutYouText: null,
                birthDateText: null,
                fillYourProfileText: null,
                usernameText: null,
                cancelText: null,
                closeText: null,
                buttonIsDisabled: true,
                snackbar: false,
                snackbarText: '',
                username: '',
                menu: false,
                modal: false,
                description: '',
                gender: null
            }
        },
        methods: {
            ...mapActions('user', ['createProfile']),

            // @vuese
            // Comprueba la validez de la información introducida
            isDataValid: function (data) {
                if (data.isValid) {
                    this.username = data.username;
                    this.birthDate = data.birthDate;
                    this.gender = data.gender;
                    this.description = data.description;

                    this.buttonIsDisabled = false;
                } else {
                    this.buttonIsDisabled = true;
                }
            },

            // @vuese
            // Guarda la información de perfil del usuario con la introducida y redirige a este al siguiente paso del proceso de registro
            setProfileInfo: function () {

                let resultCreateProfile = this.createProfile({
                    username: this.username,
                    birthDate: (new Date(this.birthDate)).setHours(0, 0, 0),
                    gender: this.gender.id,
                    description: this.description
                });

                resultCreateProfile
                    .then(() => {
                        this.$router.push('fill-your-languages');
                    })
                    .catch((error) => {
                        this.snackbarText = error;
                        this.snackbar = true;
                    });
            }
        },
        created() {
            this.genderText = this.$i18n.t('messages.TEXT_GENDER');
            this.toContinueText = this.$i18n.t('messages.TEXT_TO_CONTINUE');
            this.tellUsALittleAboutYouText = this.$i18n.t('messages.TEXT_TELL_US_A_LITTLE_ABOUT_YOU');
            this.birthDateText = this.$i18n.t('messages.TEXT_BIRTHDATE');
            this.fillYourProfileText = this.$i18n.t('messages.TEXT_FILL_YOUR_PROFILE');
            this.usernameText = this.$i18n.t('messages.TEXT_USERNAME');
            this.cancelText = this.$i18n.t('messages.TEXT_CANCEL');
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
        },
        watch: {
            // @vuese
            // Observa el nombre de usuario para eliminar los espacios en el mismo si los hubiere
            username: function () {
                this.username = this.username.trim();
            }
        },
        computed: {
            // @vuese
            // Devuelve la fecha de nacimiento mínima aceptada por la aplicación como fecha de nacimiento por defecto
            birthDatePreview: function () {
                let date = new Date();
                date.setFullYear(date.getFullYear() - minimumAge);
                return date;
            }
        }
    }
</script>

